<template>
  <div id="app">
    <IntroOverlay />
    <Navbar />
    <Hero />
    <Process v-bind:processSteps="processSteps" />
    <InstagramShowcase />
    <DedicatedExpert />
    <VerticalLine />
    <StartQuiz />
    <Footer />
  </div>
</template>

<script>
import IntroOverlay from "./components/IntroOverlay";
import Navbar from "./components/Navbar";
import Hero from "./components/Hero";
import Process from "./components/Process";
import InstagramShowcase from "./components/InstagramShowcase";
import DedicatedExpert from "./components/DedicatedExpert";
import VerticalLine from "./components/VerticalLine";
import StartQuiz from "./components/StartQuiz";
import Footer from "./components/Footer";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export default {
  name: "App",
  components: {
    IntroOverlay,
    Navbar,
    Hero,
    Process,
    InstagramShowcase,
    DedicatedExpert,
    VerticalLine,
    StartQuiz,
    Footer,
  },
  data() {
    return {
      processSteps: [
        {
          stepNumber: "01",
          stepDetails: "Complete your personalized skin profile",
        },
        {
          stepNumber: "02",
          stepDetails:
            "Schedule a consultation with one of our skincare experts",
        },
        {
          stepNumber: "03",
          stepDetails: "Review and modify your recommended products",
        },
        {
          stepNumber: "04",
          stepDetails: "Easily order the products you love straight from us",
        },
      ],
    };
  },
  mounted: function() {
    gsap.registerPlugin(ScrollTrigger);
    gsap
      .timeline()
      .from(".catchphrase", 1.5, {
        y: 50,
        skewY: 7,
        delay: 0.25,
        ease: "power4.out",
      })
      .to(".intro-overlay", 1, {
        autoAlpha: 0,
        delay: 0.5,
        ease: "power2.easeIn",
      })
      .from(".hero h1 span", 1.5, { y: "100%", ease: "power4.out" })
      .from(
        ".hero p",
        1.5,
        { autoAlpha: 0, x: "-10rem", ease: "power4.out" },
        "-=.75"
      )
      .from(
        ".hero button",
        1.5,
        { autoAlpha: 0, x: "-10rem", ease: "power4.out" },
        "-=1.5"
      )
      .from(
        ".hero-image",
        1.5,
        {
          autoAlpha: 0,
          y: "10rem",
          ease: "power4.out",
        },
        "-=1.5"
      )
      .from(".navbar", 1, { y: "-100%" }, "-=1.5");

    gsap
      .timeline({
        scrollTrigger: {
          trigger: ".process .content-wrapper",
          start: "top 65%",
        },
      })
      .from(".process header", {
        autoAlpha: 0,
        y: 25,
        duration: 0.5,
      })
      .from(".process-step", {
        autoAlpha: 0,
        x: 75,
        stagger: 0.2,
        duration: 1,
      });

    gsap
      .timeline({
        scrollTrigger: {
          trigger: ".dedicated-expert",
          start: "top 50%",
        },
      })
      .from(".dedicated-expert h2", {
        autoAlpha: 0,
        x: -75,
        duration: 1,
      })
      .from(
        ".dedicated-expert p",
        {
          autoAlpha: 0,
          x: 75,
          duration: 1,
        },
        0
      );

    const startQuizSection = document.querySelector(".start-quiz-wrapper");

    gsap
      .timeline({
        scrollTrigger: {
          trigger: startQuizSection,
          start: "top 90%",
          end: "33% 50%",
          scrub: true,
        },
      })
      .from(".start-quiz .left-img", {
        autoAlpha: 0,
        x: -150,
      })
      .from(
        ".start-quiz .right-img",
        {
          autoAlpha: 0,
          x: 150,
        },
        0
      )
      .from(
        ".start-quiz .start-quiz-text",
        {
          y: 300,
        },
        0
      );

    startQuizSection.addEventListener("mouseenter", () => {
      gsap
        .timeline()
        .to(".start-quiz .left-img", {
          scale: 1.1,
          duration: 0.25,
        })
        .to(
          ".start-quiz .right-img",
          {
            scale: 1.1,
            duration: 0.25,
          },
          0
        )
        .to(
          ".start-quiz .start-quiz-text",
          {
            scale: 1.1,
            duration: 0.25,
          },
          0
        );
    });

    startQuizSection.addEventListener("mouseleave", () => {
      gsap
        .timeline()
        .to(".start-quiz .left-img", {
          scale: 1,
          duration: 0.25,
        })
        .to(
          ".start-quiz .right-img",
          {
            scale: 1,
            duration: 0.25,
          },
          0
        )
        .to(
          ".start-quiz .start-quiz-text",
          {
            scale: 1,
            duration: 0.25,
          },
          0
        );
    });
  },
};
</script>

