<template>
  <div class="instagram-gallery">
    <hooper :settings="hooperSettings">
      <slide
        v-bind:key="galleryItem.filename"
        v-for="galleryItem in galleryItems"
      >
        <GalleryItem v-bind:galleryItem="galleryItem" />
      </slide>
      <hooper-navigation slot="hooper-addons"></hooper-navigation>
    </hooper>
  </div>
</template>

<script>
import GalleryItem from "./GalleryItem.vue";
import { Hooper, Slide, Navigation as HooperNavigation } from "hooper";
import "hooper/dist/hooper.css";

export default {
  name: "InstagramGallery",
  components: { GalleryItem, Hooper, Slide, HooperNavigation },
  props: ["galleryItems"],
  data() {
    return {
      hooperSettings: {
        itemsToShow: 1,
        infiniteScroll: true,
        autoPlay: true,
        wheelControl: false,
        keysControl: false,
        transition: "1000",
        playSpeed: "3500",
        breakpoints: {
          780: {
            itemsToShow: 2,
          },
        },
      },
    };
  },
};
</script>

<style scoped lang="scss">
.instagram-gallery {
  width: 100%;
  max-width: 120rem;

  .hooper {
    height: auto;
    width: 100%;
    padding: 0;
    outline: none;
  }
}
</style>
